<template>
  <v-dialog v-model="IsMovieVisible" max-width="400px" persistent>
    <v-card>
      <v-img small :src="movieData.coverImage" contain height="auto">
        <v-btn icon dark @click="closeModal" class="ma-2">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-img>
      <v-card-title class="black--text display-1">{{
        movieData.name
      }}</v-card-title>
      <v-card-subtitle class="ml-1 mt-2" align="left"
        ><span class="subtitle-1 font-weight-bold">Release Year:</span>
        {{ movieData.year }}</v-card-subtitle
      >
      <v-divider></v-divider>
      <v-card-text>
        <div class="subtitle-1 font-weight-bold">Plot:</div>
        <div>{{ movieData.plot }}</div>
        <v-divider></v-divider>
        <div class="subtitle-1 font-weight-bold">Actors:</div>
        <div>{{ movieData.actors }}</div>
        <v-divider></v-divider>
        <div class="subtitle-1 font-weight-bold">Producer:</div>
        <div>{{ movieData.producer }}</div>
        <v-divider></v-divider>
        <div class="subtitle-1 font-weight-bold">Genres:</div>
        <div>{{ movieData.genres }}</div>
        <v-divider></v-divider>
        <div class="subtitle-1 font-weight-bold">Total Collection:</div>
        <div>{{ movieTotalCollection }}</div>
      </v-card-text>
    </v-card>
    <v-btn icon dark @click="closeModal" class="ma-2">
      <v-icon>mdi-close</v-icon>
    </v-btn>
  </v-dialog>
</template>

<script>
export default {
  emits: ["close-movieModal"],
  props: {
    movieData: {
      type: Object,
      required: true,
    },
    isOpen: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      movieTotalCollection: "200Cr",
      IsMovieVisible: false,
    };
  },
  watch: {
    isOpen(value) {
      this.IsMovieVisible = value;
    },
  },

  methods: {
    closeModal() {
      this.IsMovieVisible = false;
      this.$emit("close-movieModal", this.IsMovieVisible);
    },
    openModal() {
      this.IsMovieVisible = this.isOpen;
    },
  },
};
</script>

<style scoped>
.v-dialog__content {
  padding: 0;
}

.v-card {
  margin: 0;
}

.v-card-title {
  font-size: 32px;
  font-weight: bold;
  padding: 24px;
}

.v-card-subtitle {
  font-size: 18px;
  padding: 0 24px 16px;
}

.v-divider {
  margin: 16px 0;
}

.v-card-text {
  padding: 24px;
}

.subtitle-1 {
  font-size: 20px;
  margin-bottom: 8px;
  font-weight: bold;
}
</style>
