export default {
  async GetGenres({ commit }) {
    
      const response = await fetch("https://localhost:44390/genres");
      const data = await response.json();
      if(response.ok)
      {
           
      commit("setGenres", data);

      }
      else{
        throw new Error("Failed to Get Genre" || data.message)
      }
   
    } 
  
};