export default {
  async addProducer({commit},newProducer) {
      const response = await fetch("https://localhost:44390/producers", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newProducer),
      });
       console.log(JSON.stringify(newProducer));
       const data = await response.json();

      if (response.ok) {
        
      

        commit("setLatestProducerId",data.id);

        commit("addProducerStore", data); // Call the mutation to add the movie to the state
      } else {
        throw new Error("Failed to add Producer" || data.message );
      }
    
  },
  async GetProducers({ commit }) {
    
      const response = await fetch("https://localhost:44390/producers");
      
      const data = await response.json();
      if(response.ok)
      {
         commit("setProducers", data);

      }
      else{
        throw new Error(data.messagen || "Failed To Get Error" )
      }
     
    }

};
