<template>
  <section>
     <loding-message v-if="isLoading"></loding-message>
    <h2>Enter Movie Details</h2>
    <movie-form
      :actor-detail="actorItems"
      :producer-detail="producerItems"
      :genre-detail="genreItems"
      @save-form="SaveData"
    ></movie-form>
    <added-success
      :show-message="successMessage"
      :success-dialog="openSuccess"
      @close-success="close"
    ></added-success>
   
  </section>
</template>

<script>
import LodingMessage from "../components/UI/LodingMessage.vue";
import AddedSuccess from "../components/movies/AddedSuccess.vue";

import MovieForm from "../components/movies/MovieForm.vue";
export default {
  components: {
    MovieForm,
    AddedSuccess,
    LodingMessage,
  },
  created() {
    this.loadActors();
    this.loadProducers();
    this.loadGenres();
  },
  data() {
    return {
      openSuccess: false,
      successMessage: null,
      isLoading: false,
    };
  },

  computed: {
    openSuccessCompute() {
      return this.openSuccess;
    },
    actorItems() {
      return this.$store.getters["actors/getactors"];
    },
    producerItems() {
      return this.$store.getters["producers/getproducers"];
    },
    genreItems() {
      return this.$store.getters["genres/getallgenres"];
    },
  },
  methods: {
    loadActors() {
      this.$store.dispatch("actors/GetActors");
    },
    loadProducers() {
      this.$store.dispatch("producers/GetProducers");
    },
    loadGenres() {
      this.$store.dispatch("genres/GetGenres");
    },
    async SaveData(formdata, UpdateOrAdd) {
      console.log(JSON.stringify(formdata));
      if (UpdateOrAdd === "Add") {
        this.isLoading = true;
        await this.$store.dispatch("movies/addMovie", formdata);
        this.isLoading = false;

        this.successMessage = "Added SuccessFully";
        this.openSuccess = true;
      } else {
        console.log("Updating");
        this.isLoading = true;
        await this.$store.dispatch("movies/updateMovie", formdata);
        this.isLoading = false;
        console.log("updated");
        this.successMessage = "Updated SuccessFuly";
        this.openSuccess = true;
      }
    },
    close() {
      this.$router.replace("/movies");
      this.openSuccess = false;
    },
  },
};
</script>
