<template>
  <div>
    <error-message ref="errorMessageRef" :error-data="errorMessage">
    </error-message>
    <v-row class="my-1 mx-4">
      <router-link to="/addMovie">
        <v-btn x-large class="mx-2" fab color="primary  white--text lighten-2">
          <v-icon> mdi-plus </v-icon>
        </v-btn>
      </router-link>
    </v-row>
    <loding-message v-if="isLoading"></loding-message>

    <v-row class="mx-2 mt-0">
      <v-col
        v-for="movie in movieDetails"
        :key="movie.id"
        cols="12"
        xs="12"
        md="4"
        xl="3"
        lg="3"
        sm="6"
        class="my-0"
      >
        <v-card
          class="card-container pa-1"
          max-width="500px"
          height="475px"
          color="blue-grey lighten-3 "
          hover
          
       
          rounded
        >
          <v-img
            max-height="200px"
            max-width="100%"
            :src="movie.coverImage"
            eager
          >
            <template v-slot:placeholder>
              <v-row class="fill-height ma-0" align="center" justify="center">
                <v-progress-circular
                  indeterminate
                  color="grey lighten-5"
                ></v-progress-circular>
              </v-row>
            </template>
          </v-img>

          <v-card-title class="font-weight-bold mt-0 text-uppercase"
            >{{ movie.name }}({{ movie.yearOfRelease }})</v-card-title
          >
          <v-divider class="mx-5 my-0"> </v-divider>

          <v-card-text class="mt-0 text-subtitle-1" align="left">
            {{ trimedPlot(movie.plot) }}
          </v-card-text>

          <v-divider class="mx-5"></v-divider>

          <v-card-actions class="footer">
            <v-row>
              <v-col cols="6" class="mx-n5 px-0">
                <v-btn
                  x-small
                  color="blue  "
                  text
                  @click="openMovieModal(movie.id)"
                >
                  Explore <v-icon small>east</v-icon>
                </v-btn>
              </v-col>

              <v-col cols="3">
                <v-btn
                  x-small
                  color="red"
                  text
                  class="ml-16 pl-6"
                  @click="showDeleteDialog(movie.id)"
                >
                  <v-icon>delete </v-icon>
                </v-btn>
              </v-col>
              <v-col class="ml-9 px-0" cols="3">
                <v-btn x-small color="blue" text @click="editForm(movie.id)"
                  ><v-icon>edit</v-icon></v-btn
                >
              </v-col>
            </v-row>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
    <div>
      <DeleteDialog
        :openDeleteDialog="deleteDialog"
        @delete="removeMovie(todeleteMovie)"
        @cancel-operation="cancelDelete"
      />
      <movie-modal
        :isOpen="openingModal"
        :movie-data="selectedMovie"
        @close-movieModal="closeModal"
      ></movie-modal>
    </div>
  </div>
</template>
<script>
import DeleteDialog from "../components/movies/DeleteDialog.vue";
import MovieModal from "../components/movies/MovieModal.vue";
import ErrorMessage from "../components/UI/ErrorMessage.vue";
import LodingMessage from "../components/UI/LodingMessage.vue";


export default {
  components: {
    DeleteDialog,
    MovieModal,
    LodingMessage,
    ErrorMessage,
  },
  created() {
    this.loadData();
  },
  mounted() {
    if (this.$route.query.search) {
      console.log(this.$route.query.search);
      this.movieDetails.filter((m) => {
        return this.$route.query.search === m.id;
      });
    }
  },

  data() {
    return {
      openingModal: false,
      deleteDialog: false,
      todeleteMovie: null,
      selectedMovie: {},
      movieCount: 0,
      isLoading: false,
      errorMessage: "",
    };
  },

  computed: {
    movieDetails() {
      return this.$store.getters["movies/getallmovies"];
    },
  },

  methods: {
    editForm(movieId) {
      this.$router.push("movies/" + movieId+"/updateMovie");
    },
    trimedPlot(plot) {
      if (plot.length <= 100) {
        return plot;
      } else {
        return plot.substring(0, 180) + "...more";
      }
    },
    async loadData() {
      this.isLoading = true;
      try {
        await this.$store.dispatch("movies/GetMovies");
        this.isLoading = false;
      } catch (e) {
        this.errorMessage = e.message || "something Wrong Happend";
        this.$refs.errorMessageRef.dialogVisible = true;
      }
    },
    removeMovie(movieId) {
      this.$store.dispatch("movies/deleteMovie", movieId);
    },

    showDeleteDialog(movieId) {
      this.todeleteMovie = movieId;
      this.deleteDialog = true;
    },
    openMovieModal(movieId) {
      const movie = this.$store.getters["movies/getallmovies"].find(
        (m) => m.id === movieId
      );
      this.selectedMovie = {
        coverImage: movie.coverImage,
        name: movie.name,
        actors: movie.actors.map((a) => a.name).toString(),
        producer: movie.producer.name,
        genres: movie.genres.map((g) => g.name).toString(),
        plot: movie.plot,
        year: movie.yearOfRelease,
      };
      this.openingModal = true;
    },
    closeModal(input) {
      this.openingModal = input;
    },
    cancelDelete(input) {
      this.deleteDialog = input;
    },
  },
};
</script>
<style scoped>
/* .container{
  background-color: black;
} */
.card-container {
  transition: transform 0.3s ease;
  width: 100%;
  overflow: hidden;
}

.card-container:hover {
  transform: scale(1.05);
}
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: rgb(196, 196, 203);
}
</style>
