<template>
  <div>
    <v-dialog v-model="dialogVisible" max-width="500px">
      <v-card :color="cardColor">
        <v-card-title class="headline"> Error! </v-card-title>

        <v-card-text class="red--text lighten-2">
          {{ errorData }}
        </v-card-text>

        <v-card-actions>
          <v-btn color="primary white--text" @click="closeDialog">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  props: ["errorData"],
  data() {
    return {
      dialogVisible: false, // Variable to control dialog visibility
      // Error message to be displayed
      cardColor: "blue lighten-3", // Color odf the card (can be customized)
    };
  },
  methods: {
    closeDialog() {
      this.dialogVisible = false;
    },
  },
};
</script>
