<template>
  <v-dialog  v-model="dialogVisible" max-width="500px" persistent>
    <v-card>
      <v-card-title class="headline">Confirmation</v-card-title>
      <v-card-text>
        Are you sure you want to delete this movie?
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="red" text @click="cancel">Cancel</v-btn>
        <v-btn color="green" text @click="confirm">Confirm</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  emits:['delete','cancel-operation'],
  props:{
    openDeleteDialog:{
      type:Boolean,
      required:true,
    }
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  watch:{
    openDeleteDialog(value)
    {
      this.dialogVisible=value;
    }

  },
  methods: {
    cancel() {
      this.dialogVisible = false;
      this.$emit('cancel-operation',this.dialogVisible)
    },
    confirm() {
      this.dialogVisible = false;
      this.$emit('delete',this.dialogVisible);
      
    },
  },
};
</script>
