// store/index.js
import Vue from "vue";
import Vuex from "vuex";
import ActorModule from "./modules/actors/index.js";
import ProducerModule from './modules/producers/index.js'
import MovieModule from './modules/movies/index.js'
import GenresModule from './modules/genres/index.js'

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {
    actors: ActorModule,
    producers:ProducerModule,
    movies:MovieModule,
    genres:GenresModule
  },
 
});

export default store;
