<template>
  <v-dialog v-model="ModalOpen" persistent max-width="400px">
    <v-card>
      <v-btn icon @click="closeModal">
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <div class="title "  >
        <slot name="title"></slot>
      </div>
      <v-divider></v-divider>
      <div class="subTitle">
        <slot name="subTitle"></slot>
      </div>

      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
        @submit.prevent="submitModalData"
      >
        <v-text-field
          v-model="name"
          :rules="nameRules"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="enteredBio"
          label="About Bio"
          :rules="BioRules"
          required
        ></v-text-field>

        <v-text-field
          v-model="enteredDob"
          type="date"
          label="Date Of Birth"
          :rules="dobRules"
          required
        ></v-text-field>

        <v-card-text>
          <v-row>
            <v-col cols="6" sm="12" md="12">
              <v-radio-group v-model="selectedGender" row required>
                <v-radio label="Male" value="Male"></v-radio>
                <v-radio label="Female" value="Female"></v-radio>
                <v-radio label="Others" value="Other"></v-radio>
              </v-radio-group>
            </v-col>
            <v-col cols="12" align="left">
               <v-btn type="submit" class="blue white--text lighten-2 pa-4  " large rounded  @click="validate">Add</v-btn>

            </v-col>
          </v-row>
        </v-card-text>

       
      </v-form>
    </v-card>
  </v-dialog>
</template>
<script>
import moment from "moment";
export default {
  emits: ["add-modalData", "close-modal"],
  props: {
    isOpen: {
      type: Boolean,
      required: true,
    },
  },

  data() {
    return {
      ModalOpen: true,
      id: 0,
      valid: false,
      name: "",
      nameRules: [(v) => !!v || "Name is required"],
      enteredBio: "",
      BioRules: [(v) => !!v || "Enter Valid Bio"],
      enteredDob: null,
      dobRules: [(v) => !!v || "Dob is required"],
      selectedGender: "Male",
      genderRules: [(v) => !!v || "Genre is required"],
    };
  },
  computed: {
    changedateTime() {
      return moment(this.enteredDob)
        .format("YYYY-MM-DDTHH:mm:ss.ms")

    },
  },
  methods: {
    closeModal() {
      this.ModalOpen = false;
      this.$emit("close-modal");
    },

    submitModalData() {
      if (this.valid) {
        var modal = {
          id: 0,
          name: this.name,
          bio: this.enteredBio,
          dob: this.changedateTime.toString(),
          gender: this.selectedGender,
        };
        this.$emit("add-modalData", modal);
        this.$refs.form.reset();
      }
    },

    validate() {
      this.$refs.form.validate();
    },
  },
};
</script>
<style scoped>



 
</style>
