export default {
  async addActor({commit},newActor) {
  
      const response = await fetch("https://localhost:44390/actors", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(newActor),
      });
      const data = await response.json();
      if (response.ok) {
        
        commit("setLatestActorId",data.id);

        commit("addActorStore", data); 
      } else {
        throw new Error("Failed to add Actor" || data.message);
      }
    
    
  },
  async GetActors({ commit }) {
    
      const response = await fetch("https://localhost:44390/actors");
      const data = await response.json();
      if(response.ok)
      {

        
        commit("setActors", data);
      }
      else{
        throw new Error(data.message || 'Failed to get Actor')
      }
      
    } 
  

};
