<template>

  <v-dialog  v-model="successDialog" max-width="500px" persistent>
    <v-card>
      <v-card-title class="headline"  >Congratulations</v-card-title>
      <v-card-text align="left">
        {{showMessage}}
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn color="blue" text @click="confirm">Close</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  props:['showMessage'],
  emits:['close-success'],
  data() {
    return {
      successDialog: false,
    };
  },
  watch:{
    showMessage(value)
    {
      this.successDialog=value;
    }
  },
  methods: {
    
    confirm() {
      this.successDialog=false;
      
      this.$emit('close-success');
    },
  },
};
</script>
