<template>
  <section>
    <h1>Actors</h1>
    <v-row class="mx-2 mt-0">
      <v-col
        v-for="actor in actorsDetails"
        :key="actor.id"
        cols="12"
        md="4"
        lg="3"
        xs="12"
      >
        <v-card
          class="card-container hide-overflow"
          max-width="400px"
          height="300px"
          color="grey lighten-2 "
        >
          <v-card-title class="font-weight-bold mt-0">{{
            actor.name
          }}</v-card-title>
          <v-divider class="mx-5 my-0"></v-divider>
          <v-card-text class="mt-0 text-subtitle-1" align="left">
            <span class="font-weight-bold">Bio:</span>
            {{ trimedBio(actor.bio) }}
          </v-card-text>
          <v-divider class="mx-5"></v-divider>
          <v-card-text class="mt-0 text-subtitle-1" align="left">
            <span class="font-weight-bold">{{ actor.gender }}</span>
          </v-card-text>
          <v-divider class="mx-5"></v-divider>
          <v-card-text class="mt-0 text-subtitle-1" align="left">
            <span class="font-weight-bold">Date Of Birth:</span
            >{{ formattedDOB(actor.dob) }}
          </v-card-text>

          <v-divider class="mx-5"></v-divider>
        </v-card>
      </v-col>
    </v-row>
  </section>
</template>
<script>
export default {
  created() {
    this.loadactor();
  },
  computed: {
    actorsDetails() {
      return this.$store.getters["actors/getactors"];
    },
  },
  methods: {
    loadactor() {
      this.$store.dispatch("actors/GetActors");
    },
    formattedDOB(dob) {
      const date = new Date(dob);
      return `${date.getDay()}-${date.getMonth()}-${(date.getFullYear() + 1)
        .toString()
        .padStart(2, "0")}`;
    },
    trimedBio(Bio) {
      if (Bio.length <= 50) {
        return Bio;
      } else {
        return Bio.substring(0, 50) + "...more";
      }
    },
  },
};
</script>
<style scoped>
.hide-overflow {
  overflow: hidden;
}
</style>
