
import Vue from "vue";
import VueRouter from "vue-router";
import MovieList from './pages/MovieList.vue'
import MovieRegistration from './pages/MovieRegistration.vue'
import ActorList from './pages/ActorList.vue'
import ProducerList from './pages/ProducerList.vue'
import NotFound from "./NotFound.vue";


Vue.use(VueRouter);

const routes = [
  {path: '/',redirect: '/movies'},
  {path:'/movies',component:MovieList},
  {path:'/actors',component:ActorList},
  {path:'/producers',component:ProducerList},
  {path:'/addMovie',component:MovieRegistration},
  {path:'/movies/:id/updateMovie',component:MovieRegistration},
  {path:'/:notFound(.*)',component:NotFound}
]

const router = new VueRouter({
  mode:'history',
  

  routes,
});

export default router;