<template>
  <section>
    <h1>Producers</h1>
    <v-row class="mx-2 mt-0">
      <v-col
        v-for="producer in producersDetails"
        :key="producer.id"
        cols="12"
        md="4"
        lg="3"
        xs="12"
        sm="12"
      >
        <v-card
          class="card-container"
          max-width="400px"
          height="300px"
          color="grey lighten-2 "
        >
          <v-card-title class="font-weight-bold mt-0">{{
            producer.name
          }}</v-card-title>
          <v-divider class="mx-5 my-0"></v-divider>
          <v-card-text class="mt-0 text-subtitle-1" align="left">
            <span class="font-weight-bold">Bio:</span>
            {{ trimedBio(producer.bio) }}
          </v-card-text>
          <v-divider class="mx-5"></v-divider>
          <v-card-text class="mt-0 text-subtitle-1" align="left">
            <span class="font-weight-bold">{{ producer.gender }}</span>
          </v-card-text>
          <v-divider class="mx-5"></v-divider>
          <v-card-text class="mt-0 text-subtitle-1" align="left">
            <span class="font-weight-bold">Date Of Birth:</span
            >{{ formattedDOB(producer.dob) }}
          </v-card-text>

          <v-divider class="mx-5"></v-divider>
        </v-card>
      </v-col>
    </v-row>
  </section>
</template>
<script>
export default {
  computed: {
    producersDetails() {
      return this.$store.getters["producers/getproducers"];
    },
  },
  methods: {
    loadproducer() {
      this.$store.dispatch("producers/GetProducers");
    },
    formattedDOB(dob) {
      const date = new Date(dob);
      return `${date.getDay()}-${date.getMonth()}-${(date.getFullYear() + 1)
        .toString()
        .padStart(2, "0")}`;
    },
    trimedBio(Bio) {
      if (Bio.length <= 50) {
        return Bio;
      } else {
        return Bio.substring(0, 50) + "...more";
      }
    },
  },
  created() {
    this.loadproducer();
  },
};
</script>
<style scoped></style>
