<template>
  <section>
    <v-container class="container">
      <v-form
        ref="form"
        v-model="valid"
        lazy-validation
        @submit.prevent="SubmitForm"
      >
        <v-text-field
          v-model="name"
          :rules="nameRules"
          label="Name"
          required
        ></v-text-field>

        <v-text-field
          v-model="year"
          type="number"
          label="Year of Release"
          :rules="yearRules"
          required
        ></v-text-field>

        <v-row>
          <v-col cols="12" xl="9" lg="9" sm="6" md="6" xs="6" >
            <v-select
              v-model="selectedActor"
              :items="actorDetail"
              item-text="name"
              item-value="id"
              label="Actors"
              multiple
              :rules="actorRules"
              :menu-props="{ maxWidth: '500px' }"
              required
            ></v-select>
          </v-col>
          <v-col cols="12" xl="3" lg="3" sm="6" md="6" xs="6" align="left" > 
            <v-btn class="model-btn blue white--text" @click="openActorModal">Add Actor</v-btn>
          </v-col>
        </v-row>

        <v-row>
          <v-col cols="12" xl="9" lg="9" sm="6" md="6" xs="6" >
            <v-select
              v-model="selectedProducer"
              :items="producerDetail"
              item-text="name"
              item-value="id"
              label="Producers"
              :rules="producerRules"
              :menu-props="{ maxWidth: '500px' }"
              required
            ></v-select>
          </v-col>

          <v-col cols="12" xl="3" lg="3" sm="6" md="6" xs="6" align="left"  >
            <v-btn class="model-btn blue white--text" @click="openProducerModal"
              >Add Producer</v-btn
            >
          </v-col>
        </v-row>

        <v-select
          v-model="selectedGenre"
          :items="genreDetail"
          item-text="name"
          item-value="id"
          label="Genres"
          multiple
          :rules="genreRules"
          :menu-props="{ maxWidth: '800px' }"
          required
        ></v-select>

        <v-textarea
          v-model="plot"
          type="textarea"
          label="Plot"
          rows="3"
          :rules="plotRules"
          required
        ></v-textarea>

        <v-file-input
          v-model="selectedFile"
          label="Choose a poster"
          :value="fileUrl"
          :rules="fileRules"
          @change="toUpdatePoster"
        ></v-file-input>

        <v-row>
          <v-btn
            color="white--text"
            class="mt-4 ml-4 fancy-btn"
            elevation="2"
            rounded
            type="submit"
            @click="validate"
            min-width="200px"
            >{{ isCreate }}
          </v-btn>
        </v-row>
      </v-form>
    </v-container>

    <modal-form
      v-if="isOpenActor"
      :modal-open="isOpenActor"
      @close-modal="closeAddActorModal"
      @add-modalData="addActor"
    >
      <template #title>
        <h3>Add Actor</h3>
      </template>
      <template #subTitle> <h4>Enter Actor Details</h4> </template>
    </modal-form>
    <modal-form
      v-if="isOpenProducer"
      :modal-Open="isOpenProducer"
      @close-modal="closeAddProducerModal"
      @add-modalData="addProducer"
    >
      <template #title>
        <h3>Add Producer</h3>
      </template>
      <template #subTitle> <h4>Enter Producer Details</h4> </template>
    </modal-form>
  </section>
</template>
<script>
import ModalForm from "../Models/ModalForm.vue";

export default {
  emits: ["save-form"],
  props: {
    actorDetail: {
      type: Array,
      required: true,
    },
    producerDetail: {
      type: Array,
      required: true,
    },
    genreDetail: {
      type: Array,
      required: true,
    },
  },
  components: {
    ModalForm,
  },
  data() {
    return {
      movieId: 0,
      isCreate: "Add",
      valid: false,
      name: "",
      nameRules: [(v) => !!v || "Name is required"],
      year: 1900,
      yearRules: [
        (v) => !!v || "Year is required",
        (v) =>
          (v && v >= 1900 && v <= new Date().getFullYear()) || "Invalid year",
      ],
      selectedActor: [],
      actorRules: [
        (v) => (!!v && v.length > 1) || "At least two actor is required",
      ],
      selectedProducer: "",
      producerRules: [(v) => !!v || "Producer is required"],
      selectedGenre: [],
      genreRules: [(v) => (!!v && v.length >1)  || "Genre is required select at least 2"],
      plot: "",
      plotRules: [(v) => !!v || "Plot is required"],
      selectedFile: null,
      fileRules: [(v) => !!v || "File is required"],
      isOpenActor: false,
      isOpenProducer: false,
      isLoading: false,
      fileUrl: "",
      updatePoster: false,
    };
  },
  created() {
    const id = this.$route.params.id;
    console.log(id);
    if (id && id > 0) {
      const movie = this.loadMovie(Number(id));

      this.movieId = movie.id;
      this.name = movie.name;
      this.year = movie.yearOfRelease;
      this.plot = movie.plot;
      this.selectedActor = movie.actors.map((a) => a.id);
      this.selectedProducer = movie.producer.id;
      this.selectedGenre = movie.genres.map((g) => g.id);
      this.fileUrl = movie.coverImage;
      var file = new File([], this.name + "poster.jpg", { type: "text/plain" });
      this.selectedFile = file;

      this.isCreate = "Update";
    }
  },
  methods: {
    toUpdatePoster() {
      this.updatePoster = true;
    },
    openActorModal() {
      this.isOpenActor = true;
    },
    openProducerModal() {
      this.isOpenProducer = true;
    },
    closeAddActorModal() {
      this.isOpenActor = false;
    },
    closeAddProducerModal() {
      this.isOpenProducer = false;
    },
    async addActor(data) {
      await this.$store.dispatch("actors/addActor", data);
      const id = this.$store.getters["actors/getlatestactor"];
      this.selectedActor.push(id);
      this.isOpenActor = false;
    },
    async addProducer(data) {
      await this.$store.dispatch("producers/addProducer", data);
      const id = this.$store.getters["producers/getlatestproducer"];
      this.selectedProducer = id;
      this.isOpenProducer = false;
    },

    loadMovie(id) {
      return this.$store.getters["movies/getallmovies"].find(
        (m) => m.id === Number(id)
      );
    },
    validate() {
      this.$refs.form.validate();
    },
    resetValidation() {
      this.$refs.form.resetValidation();
    },
    SubmitForm() {
      console.log("clicked at submit form");
      if (this.valid) {
        var formData = {
          id: this.movieId,
          name: this.name,
          yearOfRelease: parseInt(this.year),
          plot: this.plot,
          actorsids: this.selectedActor.toString(),
          genreids: this.selectedGenre.toString(),
          producerId: parseInt(this.selectedProducer),
        };
        if (this.updatePoster) {
          formData.coverImage = this.selectedFile;
        } else {
          formData.coverImage = this.fileUrl;
        }

        this.$emit("save-form", formData, this.isCreate);
      } else {
        setTimeout(this.resetValidation, 2000);
      }
    },
  },
};
</script>
<style scoped>
.container {
  background-color: rgb(236, 240, 244);
}

.model-btn {
  margin-top: 10px;
  width: 200px;
}
.fancy-btn {
  background: linear-gradient(315deg, #42d392 25%, #647eff);
  border: none;
  padding: 5px 10px;
  margin: 5px;
  border-radius: 8px;
  cursor: pointer;
}
</style>
