export default {
  deleteMovie({ commit }, movieId) {
    fetch(
      `https://localhost:44390/movies
/${movieId}`,
      { method: "DELETE" }
    )
      .then(() => {
        commit("deleteMovieStore", movieId);
      })
      .catch((error) => {
        console.error(error);
      });
  },
  async GetMovies({ commit }) {
    const response = await fetch("https://localhost:44390/movies");
    const data = await response.json();
    if (response.ok) {
      commit("setMovies", data);
    } else {
      throw new Error(data.message || "Failed To Get Movie");
    }
  },

  async addMovie({ commit }, movie) {
    const formData = new FormData();
    formData.append("file", movie.coverImage);

    const response = await fetch("https://localhost:44390/movies/upload", {
      method: "POST",
      body: formData,
    });
    const imageUrl = await response.text();
    if (response.ok) {
      movie.coverImage = imageUrl;
    } else {
      throw new Error(imageUrl.message || "failed To add Message");
    }

    const responseData = await fetch("https://localhost:44390/movies", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(movie),
    });
    const data = await responseData.json();

    if (response.ok) {
      commit("addMovieStore", data);
    } else {
      throw new Error("Failed to add movie" || data.message);
    }
  },

  async updateMovie({ commit }, updatedMovie) {
    console.log(updatedMovie.coverImage);
    console.log(updatedMovie.coverImage.size);
    if (updatedMovie.coverImage && updatedMovie.coverImage.size > 0) {
      const formData = new FormData();

      formData.append("file", updatedMovie.coverImage);
      console.log(formData);

      const response = await fetch("https://localhost:44390/movies/upload", {
        method: "POST",
        body: formData,
      });
      const imageUrl = await response.text();

      if (response.ok) {
        updatedMovie.coverImage = imageUrl;
      } else {
        throw new Error(imageUrl.message || "Failed To Fetch");
      }
    }

    const responseData = await fetch("https://localhost:44390/movies", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(updatedMovie),
    });

    if (responseData.ok) {
      commit("updateMovieStore", updatedMovie);
    } else {
      throw new Error("Update failed");
    }
  },
};
